/*
* Style Works for both Editor and Frontend
*/


.wp-block-aab-accordion-block {
  overflow: hidden;
  transition: all 0.3s ease-in-out;
  border-radius: 6px;

  &.aab__accordion_disabled {
    pointer-events: none !important;
  }

  a.aab__accordion_link {
    display: block;
    text-decoration: none !important;
  }

  .aab__accordion_head {
    display: flex;
    cursor: pointer;
    padding: 13px 22px;

    &:focus {
      box-shadow: none;
    }

    .heading-icon {
      margin-right: 18px;

      img {
        max-width: 50px;
        height: auto;
        box-shadow: none;
      }
    }

    &.hasSubHeading {
      &.active {
        .aab__accordion_heading .head_content_wrapper {
          transform: translateY(12px);
        }

        .aab__accordion_subheading {
          opacity: 0;
          visibility: hidden;
        }
      }
    }

    &.aab__accordion_head--keyboard:focus {
      outline: none;
    }

    // header icon
    .aab__accordion_icon {
      width: 20px;
      height: 20px;
      border-radius: 50%;
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 55px;

      .aab__icon {
        display: flex;
        align-items: center;
        justify-content: center;
        height: 100%;
      }
    }

    // header icon position
    &.aab_right_icon {
      flex-direction: row;
    }

    &.aab_left_icon {
      flex-direction: row-reverse;
    }

    // heading
    .aab__accordion_heading {
      display: flex;
      flex: 1;
      align-items: center;

      .head_content_wrapper {
        transition: all 0.3s ease-in-out;

      }

      .aab__accordion_subheading {
        transition: all 0.2s ease-in-out;
      }

      .title_wrapper {
        position: relative;
        display: flex;
        align-items: center;
        gap: 10px;
      }

      .icon-container {
        display: none;
      }

      .aab__accordion_title {
        padding-top: 0;
      }

      &.aab_right_icon {
        margin-right: 20px;
        flex: 1;
      }

      &.aab_left_icon {
        margin-left: 16px;
        flex: 1;
      }

      &.aab_left_link {
        .title_wrapper {
          flex-direction: row-reverse;
        }
      }

      a {
        text-decoration: none;
        opacity: 0.5;
        font-size: 1.4rem !important;
        line-height: 0.5;

        &.anchorjs-link {
          &:after {
            font-size: 1.4rem !important;
          }
        }
      }

      &:hover {
        a {
          opacity: 1;
        }
      }
    }
  }

  .collapse-icon span {
    display: block;
  }

  .aab__accordion_body {
    padding: 13px 22px;
    transition: border 0.3s ease-in-out;
    display: none;

    &.aab__accordion_body--show {
      display: block;
    }
  }

  .aab__accordion_component {
    p {
      &:last-child {
        margin-bottom: 4px;
      }
    }
  }

  .count {
    background: rgba(175, 184, 193, 0.2);
    height: 22px;
    color: #838793;
    font-size: 12px;
    line-height: 22px;
    padding: 0 2px;
    transition: all 0.3s;
    min-width: 20px;
    border-radius: 2em;
    font-weight: 500;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}

.feedback-btn-wrap {
  text-align: right;
  display: block;

  > span {
    margin-right: 10px;
    font-weight: 500;
    color: #6d6d6d;
  }

  .feedback-btn {
    background: transparent;
    border: none;
    margin-left: 7px;
    font-weight: bold;
    color: #353535;
    display: inline-flex;
    line-height: 23px;
    gap: 5px;
    padding: 3px;
    cursor: pointer;
    box-shadow: none;

    &.active {
      color: #006fff;
    }
  }
}

.aab-feedback-thankyou {
  position: fixed;
  bottom: 10px;
  left: 0;
  right: 0;
  margin: auto;
  z-index: 999;
  background: #000;
  color: #fff;
  width: max-content;
  padding: 5px 20px;
  font-size: 16px;
  font-weight: 500;
  border-radius: 5px;
  box-shadow: 0 1px 5px 3px #050505;
}


/* *************************************
	Q/A css
************************************** */


.wp-block-aab-accordion-block.is-style-qa {
  position: relative;

  :where(.wp-site-blocks *:focus) {
    outline-width: 1px;
    outline-style: none
  }

  .aab__accordion_head {
    display: flex;
    justify-content: space-between;
    padding: 10px 20px;
    align-items: center;

    &:focus {
      box-shadow: none;
    }

    &.aab_left_icon + .aab__accordion_body {
      padding-left: 128px;
    }

    .aab__accordion_heading {
      gap: 10px;

      .icon-container {
        display: block;
        position: relative;

        .icon-q {
          background: #505050;
          color: #fff;
          width: 80px;
          line-height: 43px;
          font-size: 25px;
          text-align: center;
          font-weight: 500;
          border-radius: 3px;
          position: relative;
          z-index: 2;
        }

        .icon-a {
          background: #f5a623;
          color: #fff;
          width: 80px;
          line-height: 43px;
          font-size: 25px;
          text-align: center;
          border-radius: 3px;
          font-weight: 500;
          position: absolute;
          top: 0;
          left: 0;
          opacity: 0;
          transition: all 0.4s ease-in-out;
          z-index: 1;
        }
      }

    }

    &.active {
      .aab__accordion_heading {
        .icon-container {
          .icon-q {
            border-radius: 3px 3px 0 0;
          }

          .icon-a {
            top: 43px;
            opacity: 1;
            border-radius: 0 0 3px 3px;
          }
        }
      }
    }
  }

  .aab__accordion_body {
    padding: 0 20px 10px 110px;
    border-top: 0;

    :first-child {
      margin-top: 0;
    }

    .block-editor-inner-blocks {
      .block-editor-block-list__layout :first-child {
        margin-top: 0;
      }
    }
  }
}

.aab__accordion_container.wp-block-aab-accordion-block {
  .aab__accordion_head {
    &.aab_left_icon {
      display: flex;
      align-items: center;
    }

    &.aab_right_icon {
      display: flex;
      align-items: center;
    }
  }
}